import { NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';

import { SharedModule } from '../../../shared/modules/shared.module';

import { ProductClassMasterRoutingModule } from './product-class-master-routing.module';
import { ProductClassMasterComponent } from './product-class-master.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ProductClassMasterRoutingModule,
  ],
  declarations: [
    ProductClassMasterComponent,
  ],
   schemas: [ NO_ERRORS_SCHEMA ]
})
export class ProductClassMasterModule { }


